import React, { useState, useEffect } from 'react';
import { Link } from 'gatsby';
import './Logout.css';

function Logout() {
  const [isLoggingOut, setIsLoggingOut] = useState(false);
  const [isLoggedOut, setIsLoggedOut] = useState(false);

  const handleLogout = () => {
    setIsLoggingOut(true);
    
    // Simulate logout process
    setTimeout(() => {
      setIsLoggingOut(false);
      setIsLoggedOut(true);
    }, 2000);
  };

  useEffect(() => {
    // Auto-logout after component mounts
    const timer = setTimeout(() => {
      if (!isLoggedOut) {
        handleLogout();
      }
    }, 1000);

    return () => clearTimeout(timer);
  }, [isLoggedOut]);

  return (
    <div className="logout-container-main">
      <div className="logout-card logout-container">
     

        <div className="logout-content">
          {!isLoggedOut ? (
            <>
              <div className="logout-icon">
                <div className="icon-wrapper">
                  {isLoggingOut ? (
                    <div className="logout-spinner"></div>
                  ) : (
                    'ðŸ‘‹'
                  )}
                </div>
              </div>
              
              <h2 className="logout-title">
                {isLoggingOut ? 'Logging Out...' : 'Goodbye!'}
              </h2>
              <p className="logout-subtitle">
                {isLoggingOut 
                  ? 'Please wait while we securely log you out.'
                  : 'You are about to be logged out of your account.'
                }
              </p>

              {!isLoggingOut && (
                <div className="logout-actions">
                  <button onClick={handleLogout} className="confirm-logout-button">
                    Confirm Logout
                  </button>
                  <Link to="" className="cancel-button">
                    Cancel
                  </Link>
                </div>
              )}
            </>
          ) : (
            <div className="success-message">
              <div className="success-icon">
                <div className="icon-wrapper success">
                  âœ“
                </div>
              </div>
              
              <h2 className="success-title">Successfully Logged Out</h2>
              <p className="success-subtitle">
                Thank you for using MagicBox. Your session has been securely ended.
              </p>
              
              <div className="success-actions">
                <Link to="" className="login-again-button">
                  Login Again
                </Link>
                <p className="security-note">
                  For your security, all session data has been cleared.
                </p>
              </div>
            </div>
          )}
        </div>

        {/* Background Animation */}
        <div className="background-animation">
          <div className="floating-circle circle-1"></div>
          <div className="floating-circle circle-2"></div>
          <div className="floating-circle circle-3"></div>
        </div>
      </div>
    </div>
  );
};

export default Logout;